footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background: black;
    padding-top: 5%;
    
    .logo{
        width: 10%;
        filter: brightness(3);

    }

    >p {
        padding-top: 2%;
        font-weight: 300;
        font-size: 18px;
        color: white;
    }
}

@media all and (min-width : 641px) and (max-width : 1100px){
    footer {
        padding: 8% 0 8% 0;
        
        .logo{
            width: 20%;
        }

        >p {
            font-size: 14px;
        }
    }
}

@media all and (max-width : 640px) {
    footer {
        padding: 10% 0 10% 0;
        
        .logo{
            width: 30%;
        }

        >p {
            font-size: 14px;
        }
    }
}