.cards_Div{
    background: rgb(246, 246, 246);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-bottom: 4%;
    padding: 1%;
}

.home_Cards{
    width: 31%;
    text-decoration: none;
    color: white;
    padding: 1%;

    >img {
        height: 100%;
        width: 100%;
        border-radius: 10px;
    }

    >h1 {
        height: 10%;
        font-size: 18px;
        z-index: 2;
        padding: 0 0 0 5%;
        width: 75%;
        margin: -75px 0 0 0; 
        }
}

@media all and (min-width : 641px) and (max-width : 1100px){
    .cards_Div{
        justify-content: center;
    }
    .home_Cards{
        width: 45%;
        height: 250px;
    
        >h1 {
            width: 90%;
            margin: -75px 0 0 0; 
            }
    }
}

@media all and (max-width : 640px) {
    .cards_Div{
        background: none;
        flex-direction: column;
    }

    .home_Cards{
        width: 100%;
        padding-bottom: 4%;
        height: 250px;
        padding-left: 0;

        >h1 {
            width: 50%;
            font-size: 14px;
            margin: -55px 0 0 0; 
        }
    }
}


