.Home_Image_Banner {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 250px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)),url(../../assets/Home_Desktop_Banner.png);
    background-size: cover;
    border-radius: 10px;
    color: white;
    margin-bottom: 3%;

    >h1 {
        font-size: 45px;
        font-weight: 500;
        padding: 1% 0 0 1%;
    }
}

.Propos_Image_Banner {
    height: 300px;
    background-size: cover;
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),url(../../assets/Propos_Desktop_Banner.png);
    border-radius: 10px;
}

@media all and (min-width : 641px) and (max-width : 1100px){
    .Home_Image_Banner {
        height: 200px;
        
        >h1 {
            width: 70%;
            font-size: 35px;
        }
    }

    .Propos_Image_Banner {
        height: 300px;
    }
}

@media all and (max-width : 640px) {
    .Home_Image_Banner {
        justify-content: flex-start;
        height: 140px;
        background-image: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)),url(../../assets/Home_Mobile_Banner.png);
        
        >h1 {
            width: 55%;
            font-size: 28px;
            line-height: 32px;
            padding: 1% 0 0 7%;
        }
    }

    .Propos_Image_Banner {
        height: 250px;
        background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),url(../../assets/Propos_Mobile_Banner.png);
    }
}