.collapse_Div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 4% 0 4% 0;

    >.collapse{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.bandeau{
    background: #FF6060;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2% 0 2%;
    border-radius: 5px;
    color: white;
    position: relative;
    z-index: 2;
    margin: 2% 0 1% 0;
    width: 76%;


    .bandeau_titre{
        margin: 0;
        font-size: 22px;
        font-weight: 500;
    }

    .bandeau_span{
        background-color:#FF6060;
        border: none;
        color: white;
        font-size: 32px;
        cursor: pointer;
    }
}

.descriptif{
    background: rgb(239, 239, 239);
    color: #FF6060;
    font-weight: 500;
    font-size: 24px;
    padding: 1% 0 0 2%;
    margin: -3% 0 0 0;
    border-bottom-left-radius: 10px ;
    border-bottom-right-radius: 10px ;
    z-index: 1;
    width: 78%;
}

@media all and (min-width : 641px) and (max-width : 1100px){
    
    .collapse_Div{
        margin-top: 10%;
        margin-bottom: 16%;
    }

    .bandeau{
        margin: 3% 0 2% 0;
    }

    .descriptif{
        font-size: 20px;
    }
}

@media all and (max-width : 640px) {
    .collapse_Div{
        margin: 12% 0 21% 0;
    }

    .bandeau{
        width: 95%;
        margin: 5% 0 5% 0;

        .bandeau_titre{
            font-size: 16px;
        }

        .bandeau_span{
            font-size: 24px;
        }
    }

    .descriptif{
        font-size: 14px;
        padding: 3% 2% 5% 3%;
        margin: -6% 0 8% 0;
        width: 94%;
    }

}