.renseignement_collapse {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: -3%;
    margin-bottom: 5%;

    .collapse_description{
        width: 50%;
        
    }

    .collapse_equipement{
        display: flex;
        justify-content: end;
        flex-direction: column;
        width: 50%;
        margin-right: -35px;
    }
}

.barre{
    background: #FF6060;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3% 0 3%;
    border-radius: 10px;
    color: white;
    position: relative;
    z-index: 2;
    margin: 2% 0 2% 0;
    width: 90%;

    .barre_titre{
        margin: 0;
        font-size: 22px;
        font-weight: 500;
    }

    .barre_span{
        background-color:#FF6060;
        border: none;
        color: white;
        font-size: 32px;
        cursor: pointer;
    }
}

.deroulant{
    background: rgb(239, 239, 239);
    color: #FF6060;
    font-weight: 500;
    font-size: 20px;
    padding: 1% 1% 2% 2%;
    margin: -3% 0 0 0;
    border-bottom-left-radius: 10px ;
    border-bottom-right-radius: 10px ;
    z-index: 1;
    width: 93%;
    list-style: none;
}


@media all and (min-width : 641px) and (max-width : 1100px){
    .renseignement_collapse{
        flex-direction: column;

        .collapse_description{
            width: 100%;
        }

        .collapse_equipement{
            width: 100%;
        }
    }

    .barre{
        width: 90%;  
    }

    .descriptif{
        width: 100%;
        font-size: 20px;
    }
}

@media all and (max-width : 640px) {
    .renseignement_collapse{
        flex-direction: column;
        margin: 5% 0 23% 0;

        .collapse_description{
            width: 100%;
        }

        .collapse_equipement{
            width: 100%;
        }
    }

    .collapse_Div{
        margin: 10% 0 14% 0;
    }

    .barre{
        width: 94%;
    }

    .deroulant{
        font-size: 14px;
        padding: 3% 2% 5% 3%;
        margin: -6% 0 8% 0;
        width: 95%;
    }

}